import * as T from 'prop-types';

/**
 * data point
 */
export const point = ({ types = [], metaShape = {} }) => {
    return T.shape({
        /**
         * type of this data point
         */
        type: T.oneOf(types).isRequired,
        /**
         * value of this data point
         */
        val: T.oneOfType(types).isRequired,
        /**
         * field name if applicable
         */
        field: T.string,
        /**
         * raw value if applicable
         */
        _raw: T.any,
        /**
         * metadata associated with this data point
         */
        meta: T.shape(metaShape),
    });
};
/**
 * data series
 */
export const series = dp => {
    return T.arrayOf(dp);
};
/**
 * data frame
 */
export const frame = series => {
    return T.arrayOf(series);
};
